.section__padding {
    padding: 4rem 6rem;
}
.flex__center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.app__bg {
    background: url('./assets/bg.png');
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
    background-attachment: fixed;
}

.app__wrapper {
    display:flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
}
.app__wrapper_info{
    flex:1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width:100%
}

.app__wrapper_img{
    flex:1;
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left:2rem;
}

.app__wrapper_img-reverse{
    justify-content: flex-start;
    margin-right: 2rem;
}

.app__wrapper_img img{
    width:80%;
}

.custom__button{
    background-color: var(--color-golden);
    color:var(--color-black);
    font-family: var(--font-base);
    font-weight:700;
    font-size: 16px;
    line-height: 175%;
    letter-spacing: 0.04em;
    padding:0.5rem 1.5rem;
    border-radius: 1px;
    border:none; outline: none;
    cursor:pointer;
}
.custom__button:hover{
    background-color: var(--color-black);
    color:var(--color-white);
    border: .2px solid var(--color-golden);
    outline: none;
}

.p__cormorant{
    font-family: var(--font-base);
    color:var(--color-white);
    font-weight: 700;
    font-size: 23px;
    line-height: 30px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-feature-settings: 'tnum' on, 'lnum' on;
}
.p__opensans{
    font-family: var(--font-alt);
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.04em;
    color:var(--color-white);
    text-transform: capitalize;
}

.headtext__cormorant {
    font-family: var(--font-base);
    color: var(--color-golden);
    font-size: 64px;
    line-height: 83.2px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
}
.spoon__img {
    width: 45px;
}

@media screen and (min-width: 2000px) {
    .p__opensans{
        font-size: 24px;
        line-height: 32px;
    }
}
@media screen and (max-width: 1150px) {
        .app__wrapper {
        flex-direction: column;
        }
    
        .app__wrapper_img {
        margin: 5rem 0 0 0;
        }
    
        .app__wrapper_img-reverse {
        margin: 0 0 5rem 0;
        }
    
        .app__wrapper_img img {
        width: 100%;
        }
    }
    
    @media screen and (max-width: 850px) {
        .section__padding {
        padding: 4rem;
        }
    }
    
    @media screen and (max-width: 650px) {
        .section__padding {
        padding: 4rem 2rem;
        }
        
        .p__cormorant {
        font-size: 21px;
        }
    
        .p__opensans {
        font-size: 14px;
        }
    }
    
    @media screen and (max-width: 450px) {
        .p__opensans {
        font-size: 12px;
        }
    
        .p__cormorant {
        font-size: 19px;
        }
    
        .headtext__cormorant {
        font-size: 45px;
        line-height: 70px;
        }}